<template>
  <div>
    <a-button size="small" type="primary" :style="{marginTop: '4px'}" @click="showModal">
      <slot>
        <a-icon type="plus" />
      </slot>
    </a-button>
    <a-modal
      title=""
      :visible="visible"
      :confirmLoading="confirmLoading"
      :footer="null"
      @cancel="handleCancel"
    >
      <a-form :form="form" @submit="handleSubmit">
        <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 18 }">
          <a-select
            showSearch
            placeholder=""
            v-decorator="['type', { rules: [{ required: true, message: '' }] }]"
          >
            <a-select-option value="1"></a-select-option>
            <a-select-option value="2"></a-select-option>
          </a-select>
        </a-form-item>

        <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 18 }">
          <a-input
            v-decorator="['money', { rules: [{ required: true,type: 'number',                    transform(value) {
                      if(value){
                        return Number(value);
                      }
                    }, message: '' }] }]"
            placeholder=""
          />
        </a-form-item>
        <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 18 }">
          <a-select
            v-decorator="[
          'account_type',
          { rules: [{ required: true, message: '' }] },
        ]"
            placeholder=""
          >
            <a-select-option value="1"></a-select-option>
            <a-select-option value="2"></a-select-option>
            <a-select-option value="3"></a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 18 }">
          <a-input
            v-decorator="['account', { rules: [{ required: true, message: '' }] }]"
            placeholder=""
          />
        </a-form-item>
        <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 18 }">
          <a-input
            v-decorator="['real_name', { rules: [{ required: true, message: '' }] }]"
            placeholder=""
          />
        </a-form-item>
        <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 18 }">
          <a-textarea
            v-decorator="['description']"
            placeholder=""
            :autosize="{ minRows: 1, maxRows: 3 }"
          />
        </a-form-item>
        <a-form-item :wrapper-col="{ span: 8, offset: 18 }">
          <a-row>
            <a-col :span="8" :style="{ textAlign: 'left' }">
              <a-button size="small" @click="handleCancel"></a-button>
            </a-col>
            <a-col :span="8" :style="{ textAlign: 'left' }">
              <a-button size="small" type="primary" html-type="submit"></a-button>
            </a-col>
          </a-row>
        </a-form-item>
      </a-form>
    </a-modal>
  </div>
</template>
<script>
import { apply } from "@/api/money";
export default {
  props: {
    okText: {
      type: String,
      default: ""
    },
    cancelText: {
      type: String,
      default: ""
    },
    onAdd: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      visible: false,
      confirmLoading: false,
      formLayout: "horizontal",
      form: this.$form.createForm(this, { name: "coordinated" }),
      roles: []
    };
  },
  created() {
    // this.getRoleList();
  },
  methods: {
    showModal() {
      this.visible = true;
    },
    handleCancel(e) {
      console.log("Clicked cancel button");
      this.visible = false;
      this.form.resetFields();
    },

    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log(values)
          this.confirmLoading = true;
          apply(values).then(response => {
            console.log(response)
            this.onAdd();
            this.$message.success(response.message);
            this.visible = false;
            this.confirmLoading = false;
            this.form.resetFields();
          });
        }
      });
    }
  }
};
</script>



